@import '../../style/style.scss';

.App {
  background: #FFFFFF;
  padding: 15px;
}

.page {
  width: 700px;
  margin: 0 auto;
}

@media (max-width: 700px) {
  .page {
    width: 500px;
  }
}

@media (max-width: 500px) {
  .page {
    width: 400px;
  }
}

@media (max-width: 400px) {
  .page {
    width: 350px;
  }
}

@media (max-width: 350px) {
  .page {
    width: 300px;
  }
}