@import '../../style/style.scss';

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
}

.logo {
  width: 100px;
  height: 100px;
  border-radius: 20px;
}

.header__nav {
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 900;
  font-size: 20px;
  margin-right: 30px;
  color: $black;
  transition: all .5s;
  &:hover {
    color: $blue;
  }
}

.header__mobile-menu {
  position: fixed;
  right: 55px;
  top: 50px;
  width: 35px;
  height: 20px;
  display: none;
  border-bottom: 3px solid $black;
  cursor: pointer;
  &:before {
    position: absolute;
    content: "";
    top: 50%;
    left: 0;
    width: 100%;
    height: 3px;
    background: $black;
  }
  &:after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: $black;
  }
}

.header__nav-desc-mobile {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 10px;
  top: 80px;
  padding: 5px;
  transition: 1s;
  perspective-origin: top;
  transform-origin: top;
  transform: perspective(100px) rotateX(0deg);
  border: 1px solid $black;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  background: #FFFFFF;
  animation: mobileMount ease-in-out .5s;
}

.header__nav-mobile {
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 900;
  font-size: 20px;
  color: $black;
  transition: all .5s;
  margin-bottom: 10px;
  &:hover {
    color: $blue;
  }
}

@keyframes mobileMount {
  0%{transform: perspective(100px) rotateX(-90deg);}
  100%{transform: perspective(100px) rotateX(0deg);}
}

@media (max-width: 600px) {
  .header__nav {
    margin-right: 20px;
    font-size: 16px;
  }
}

@media (max-width: 500px) {
  .header__nav-desc {
    display: none;
  }
  .header__mobile-menu {
    display: block;
  }
}

