

.gallery {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
  padding: 1em;
}

.gallery__img {
  width: 250px;
  height: 250px;
}

@media (max-width: 600px) {
  .gallery__img {
    width: 200px;
    height: 200px;
  }
}
@media (max-width: 500px) {
  .gallery__img {
    width: 150px;
    height: 150px;
  }
}

@media (max-width: 370px) {
  .gallery__img {
    width: 120px;
    height: 120px;
  }
}

@media (max-width: 300px) {
  .gallery__img {
    width: 100px;
    height: 100px;
  }
}